.navbar {
  background-color: #333;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  color: rgb(197, 58, 58);
  margin: 0;
  font-size: 24px;
}

.nav-links {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav-links li {
  margin-right: 20px;
}

.nav-links li:last-child {
  margin-right: 0;
}

.nav-links li a {
  color: rgb(20, 201, 218);
  text-decoration: none;
  font-size: 18px;
}

.nav-links li a:hover {
  color: #ddd;
}
